import './members.scss';
import React from 'react';
import Page from 'layout/page';
import SEO from 'components/seo';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { Members as MemberData } from 'src/constants';

const getAllowedLinkList = member => {
  return (
    <>
      {member.blog ? (
        <li>
          <a href={member.blog} target="_blank" rel="noopener noreferrer">
            <i className="fas fa-blog"></i>
          </a>
        </li>
      ) : null}
      {member.github ? (
        <li>
          <a href={member.github} target="_blank" rel="noopener noreferrer">
            <i className="fab fa-github"></i>
          </a>
        </li>
      ) : null}
      {member.linkedin ? (
        <li>
          <a href={member.linkedin} target="_blank" rel="noopener noreferrer">
            <i className="fab fa-linkedin"></i>
          </a>
        </li>
      ) : null}
    </>
  );
}

const Members = () => {
  return (
    <Page pageName="members">
      <SEO title="Members" />
      <Container>
        <h1>Members</h1>
        <Row id="member-list">
          {MemberData.map((member, index) => {
            return (
              <Col key={index} xs={12} sm={6} md={4}>
                <Card className="contents-card">
                  <Card.Img src={member.profileImg} />
                  <Card.Body>
                    <Card.Title>{member.name}</Card.Title>
                    <Card.Subtitle>{member.role}</Card.Subtitle>
                    <ul className="member-links">
                      {getAllowedLinkList(member)}
                    </ul>
                  </Card.Body>
                </Card>
              </Col>
            );
          })}
        </Row>
      </Container>
    </Page>
  );
}

export default Members;
